import React from "react";
import MaterialTable from "material-table";
import {useHistory} from "react-router";
import {tableIcons} from "../Component/MaterialTable/tableIcons";
import {localtime_exact} from "../controller/utils";
import TagChip from "../Component/TagChip/TagChip";

export default function TasksTable(props) {
    const history = useHistory();
    const {tasks} = props;

    return (
        <MaterialTable icons={tableIcons} columns={[
            {title: "", field: "mname", defaultGroupOrder: 1},
            {title: "", field: "cname"},
            {title: "", field: "name"},
            {title: "", field: "type", lookup: {
                    0: "",
                    1: "",
                    2: "",
                    3: "",
                    4: "",
                    5: "",
                    6: "",
                }, defaultGroupOrder: 0},
            {title: "", field: "tags", grouping: false, render: task =>
                <>{task["tags"].map((tag, key) => {
                    return <TagChip tag={tag} size="small" key={key}/>;
                })}</>
                , sorting: false},
            {title: "", field: "last_update", render: task => localtime_exact(task["last_update"]), grouping: false},
        ]} options={{
            pageSize: 20,
            grouping: true,
        }} data={tasks} components={{
            Toolbar: props => (<></>)
        }} onRowClick={(event, rowData, togglePanel) => {
            history.push("/manga/" + rowData["mid"] + "/" + rowData["cid"] + "/" + rowData["id"]);
        }} localization={{
            grouping: {
                placeholder: "...",
                groupedBy: ":"
            },
            pagination: {
                labelDisplayedRows: '{from}{to}  {count}',
                labelRowsSelect: '',
                labelRowsPerPage: ':',
                firstAriaLabel: '',
                firstTooltip: '',
                previousAriaLabel: '',
                previousTooltip: '',
                nextAriaLabel: '',
                nextTooltip: '',
                lastAriaLabel: '',
                lastTooltip: ''
            },
        }}/>
    );
}